import React, { useState, useEffect } from 'react';
import {
  TextField,
  Button,
  Grid,
  Paper,
  Typography,
  Box
} from '@mui/material';

import { useNavigate } from 'react-router-dom';

const AddressForm = ({ onSubmit, editingAddress }) => {
  const navigate = useNavigate();
  const [isFormValid, setIsFormValid] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    receipt_name: '',
    address1: '',
    address2: '',
    address3: '',
    city: '',
    country: '',
    pincode: '',
    phone: '',
    pan: ''
  });
  

  useEffect(() => {
    if (editingAddress) setFormData(editingAddress);
  }, [editingAddress]);

  useEffect(() => {
  const requiredFields = ['name', 'address1', 'city', 'country', 'pincode', 'phone'];
  const valid = requiredFields.every(
    (field) => formData[field]?.trim() !== ''
  );
  setIsFormValid(valid);
}, [formData]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
    resetForm();
  };

  const handleCancel = () => {
    resetForm();
     navigate('/home');
  };

  const resetForm = () => {
    setFormData({
      name: '', receipt_name: '', address1: '', address2: '', address3: '',
      city: '', country: '', pincode: '', phone: '', pan: ''
    });
  };

  return (
    <Paper elevation={3} sx={{ padding: 4, marginBottom: 4 }}>
      <Typography variant="h6" gutterBottom>
        {editingAddress ? 'Edit Address' : 'Add New Address'}
      </Typography>

      <form onSubmit={handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              name="name"
              label="Name"
              fullWidth
              required
              value={formData.name}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="receipt_name"
              label="Receipt Name"
              fullWidth
              value={formData.receipt_name}
              onChange={handleChange}
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              name="address1"
              label="Address Line 1"
              fullWidth
              value={formData.address1}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="address2"
              label="Address Line 2"
              fullWidth
              value={formData.address2}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="address3"
              label="Address Line 3"
              fullWidth
              value={formData.address3}
              onChange={handleChange}
            />
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextField
              name="city"
              label="City"
              fullWidth
              value={formData.city}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              name="country"
              label="Country"
              fullWidth
              value={formData.country}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              name="pincode"
              label="Pincode"
              fullWidth
              value={formData.pincode}
              onChange={handleChange}
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <TextField
              name="phone"
              label="Phone"
              fullWidth
              value={formData.phone}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="pan"
              label="PAN"
              fullWidth
              value={formData.pan}
              onChange={handleChange}
            />
          </Grid>
        </Grid>

        <Box mt={3} display="flex" justifyContent="flex-end" gap={2}>
          <Button type="submit" variant="contained"    sx={{backgroundColor:'#7c2d12', color:'#fefcbf'}}>
            {editingAddress ? 'Update Address' : 'Add Address'}
          </Button>
          <Button type="button" variant="outlined" onClick={handleCancel}  sx={{backgroundColor:'#7c2d12', color:'#fefcbf'}}>
            Cancel
          </Button>
        </Box>
      </form>
    </Paper>
  );
};

export default AddressForm;
