import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../UI/tpn logo.png'; // Update if logo has a different name
import '../UI/Navbar.css';

const Navbar = ({ isAuthenticated, onLogout }) => (
  <nav className="navbar">
    <div className="navbar-left">
      <img src={logo} alt="TPN Solutions Logo" className="navbar-logo" />
      <span className="navbar-org">Pracheena Sampradaya Natya Nadaga Mandram, Thepperumanallur</span>
    </div>
    <div className="navbar-right">
      <Link to="/home">Home</Link>
      {isAuthenticated ? (
        <>
          <Link to="/form">Address Form</Link>
          <Link to="/list">Address List</Link>
          <Link to="/logout" onClick={onLogout}>Logout</Link>
        </>
      ) : (
        <Link to="/login">Login</Link>
      )}
    </div>
  </nav>
);

export default Navbar;