import React, { useState, useEffect } from 'react';
import { Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import Navbar from './Components/Navbar';
import HomePage from './Components/HomePage';
import AddressFormPage from './Pages/AddressFormPage';
import AddressListPage from './Pages/AddressListPage';
import LoginPage from './Components/Login';

// Protected Route component
const ProtectedRoute = ({ children, isAuthenticated }) => {
  return isAuthenticated ? children : <Navigate to="/login" />;
};

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [addresses, setAddresses] = useState([]);
  const [editingIndex, setEditingIndex] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    if (isAuthenticated) {
      fetchAddresses();
    }
  }, [isAuthenticated]);

  const fetchAddresses = async () => {
    try {
      const res = await fetch('http://localhost:4002/address');
      if (!res.ok) throw new Error(`Fetch failed: ${res.statusText}`);
      const data = await res.json();
      console.log('Fetched addresses:', data);
      if (data.status === 200 && Array.isArray(data.address)) {
        setAddresses(data.address);
      } else {
        console.warn('Unexpected response format:', data);
        setAddresses([]);
      }
    } catch (err) {
      console.error('Fetch error:', err);
      alert('Failed to fetch addresses: ' + err.message);
    }
  };

  const handleAddOrUpdate = async (data) => {
    try {
      console.log('Submitting:', data);
      // if (editingIndex !== null) {
      //   const name = addresses[editingIndex].name;
      //   const res = await fetch(`http://localhost:4002/address`, {
      //     method: 'PUT',
      //     headers: { 'Content-Type': 'application/json' },
      //     body: JSON.stringify({address:data})
      //   });
      //   if (!res.ok) throw new Error(`Update failed: ${res.statusText}`);
      //   const responseData = await res.json();
      //   console.log('Update response:', responseData);
      //   setEditingIndex(null);
      //   fetchAddresses();
      //   navigate('/list');
      // } else {
        const res = await fetch('http://localhost:4002/address', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({address:data})
        });
        if (!res.ok) throw new Error(`Add failed: ${res.statusText}`);
        const responseData = await res.json();
        console.log('Add response:', responseData);
        fetchAddresses();
        navigate('/list');
     // }
    } catch (err) {
      console.error('Error:', err);
      alert(`Failed to ${editingIndex !== null ? 'update' : 'add'} address: ${err.message}`);
    }
  };

  const handleEdit = (index) => {
    setEditingIndex(index);
    navigate('/form');
  };

  const handleDelete = async (index) => {
    try {
      const name = addresses[index].name;
      const res = await fetch(`http://localhost:4002/v1/address/${name}`, {
        method: 'DELETE'
      });
      if (!res.ok) throw new Error(`Delete failed: ${res.statusText}`);
      const responseData = await res.json();
      console.log('Delete response:', responseData);
      fetchAddresses();
    } catch (err) {
      console.error('Delete error:', err);
      alert('Failed to delete address: ' + err.message);
    }
  };

  const handleLogin = (credentials) => {
    console.log(credentials);
    setIsAuthenticated(credentials);
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    setAddresses([]);
    navigate('/home');
  };

  return (
    <>
      <Navbar isAuthenticated={isAuthenticated} onLogout={handleLogout} />
      <Routes>
        <Route path="/" element={<Navigate to="/home" />} />
        <Route path="/home" element={<HomePage />} />
        <Route path="/login" element={<LoginPage onLogin={handleLogin} isAuthenticated={isAuthenticated} />} />
        <Route
          path="/form"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated}>
              <AddressFormPage
                onSubmit={handleAddOrUpdate}
                editingAddress={editingIndex !== null ? addresses[editingIndex] : null}
              />
            </ProtectedRoute>
          }
        />
        <Route
          path="/list"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated}>
              <AddressListPage
                addresses={addresses}
                onEdit={handleEdit}
                onDelete={handleDelete}
              />
            </ProtectedRoute>
          }
        />
        <Route
          path="/logout"
          element={
            <ProtectedRoute isAuthenticated={isAuthenticated}>
              <Navigate to="/home" state={{ logout: true }} />
            </ProtectedRoute>
          }
        />
      </Routes>
    </>
  );
};

export default App;