import React from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Button,
  Box
} from '@mui/material';

const AddressList = ({ addresses, onEdit }) => {
  return (
    <Box sx={{ padding: 3 }}>
      <Typography variant="h5" gutterBottom>
        Saved Addresses
      </Typography>

      {addresses.length === 0 ? (
        <Typography>No addresses found.</Typography>
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>#</TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Address</TableCell>
                <TableCell>City</TableCell>
                <TableCell>Country</TableCell>
                <TableCell>Pincode</TableCell>
                <TableCell>Phone</TableCell>
                <TableCell>PAN</TableCell>
                <TableCell>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {addresses.map((addr, index) => (
                <TableRow key={index}>
                  <TableCell>{index + 1}</TableCell>
                  <TableCell>{addr.name} ({addr.receipt_name})</TableCell>
                  <TableCell>
                    {[addr.address1, addr.address2, addr.address3].filter(Boolean).join(', ')}
                  </TableCell>
                  <TableCell>{addr.city}</TableCell>
                  <TableCell>{addr.country}</TableCell>
                  <TableCell>{addr.pincode}</TableCell>
                  <TableCell>{addr.phone}</TableCell>
                  <TableCell>{addr.pan}</TableCell>
                  <TableCell>
                    <Button
                      variant="outlined"
                      size="small"
                      onClick={() => onEdit(index)}
                      sx={{backgroundColor:'#7c2d12', color:'#fefcbf'}}
                    >
                      Edit
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Box>
  );
};

export default AddressList;
