// AddressFormPage.jsx
import React from 'react';
import AddressForm from '../Components/AddressForm';

const AddressFormPage = ({ onSubmit, editingAddress }) => (
  <div>
    <h2>Address Entry</h2>
    <AddressForm onSubmit={onSubmit} editingAddress={editingAddress} />
  </div>
);

export default AddressFormPage;
